@import url("https://fonts.googleapis.com/css?family=Poppins:100,100i,200,200i,300,300i,400,600&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

:root {
  --main-blue-color: #577bf9;
  --main-black-color: black;
  --main-grey-color: #dde0ed; }

*, *::before, *::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box;
  font: var(--font-size-body, 15.2px)/1.5 "Poppins", sans-serif; }

body, table, form, select, option, input, textarea {
  font: inherit;
  box-sizing: inherit; }

body {
  background: var(--main-background-color); }

h1 {
  font-size: 2.4em; }

h2 {
  font-size: 2.0em; }

h3 {
  font-size: 1.8em; }

h4 {
  font-size: 1.4em; }

h5 {
  font-size: 1.2em; }

h6 {
  font-size: 1.0em; }

a {
  text-decoration: none;
  color: var(--color-link-default, #9c9bb4); }

ol > li > ol > li {
  list-style-type: lower-roman; }

select {
  padding: 1em;
  margin: 1em;
  background: none;
  border-radius: 0.5em;
  outline: none;
  border: 2px solid var(--main-purple-color);
  display: block; }
  select:hover {
    border-color: #000; }
  select:focus {
    border-color: #000; }

option {
  padding: 1em;
  border-radius: 2em; }

body {
  background-image: linear-gradient(to bottom right, #ECE8E5, #D7D2CE); }

.box {
  max-width: 100em;
  min-height: 700px;
  margin: 8em auto;
  background: #F4F4F4;
  border-radius: 1.5em;
  padding: 3em;
  display: flex; }
  .box-content {
    flex: 0 0 40%;
    display: flex;
    flex-wrap: wrap; }
    .box-content .page-nav {
      width: 100%; }
    .box-content .page-nav ul li {
      display: inline-block;
      margin-right: 2em; }
      .box-content .page-nav ul li a {
        text-transform: uppercase;
        color: #444444; }
      .box-content .page-nav ul li .page-logo {
        font-size: 1.5em;
        font-weight: 800;
        margin-right: 1em; }
    .box-content .page-content {
      align-items: flex-end;
      margin-top: auto; }
      .box-content .page-content h2 {
        margin-bottom: 0.5em;
        max-width: 50%;
        color: #444444;
        font-size: 2.5em; }
      .box-content .page-content p {
        margin-bottom: 3em;
        font-weight: 200;
        max-width: 50%; }
      .box-content .page-content .button-primary {
        border: 1px solid #08587B;
        font-weight: 800;
        color: #08587B;
        border-radius: 0.5em;
        padding: 0.7em 2.5em;
        text-transform: uppercase;
        background: none;
        cursor: pointer; }
  .box-graphics {
    flex: 0 0 60%;
    display: flex;
    flex-wrap: wrap; }
    .box-graphics .row {
      display: flex;
      flex-wrap: wrap; }
    .box-graphics .circle {
      width: 14em;
      height: 14em;
      border-radius: 50%; }
    .box-graphics .quarter-circle {
      width: 14em;
      height: 14em; }
    .box-graphics .top {
      border-radius: 14em 0 0 0; }
    .box-graphics .bottom-left {
      border-radius: 0 0 0 14em; }
    .box-graphics .bottom-right {
      border-radius: 0 0 14em 0; }
    .box-graphics .dark-blue-color {
      background: #1E5268; }
    .box-graphics .light-blue-color {
      background: #90D4DD; }
    .box-graphics .pink-color {
      background: #ECB0B0; }
    .box-graphics .yellow-color {
      background: #E3D5BB; }
    .box-graphics .green-color {
      background: #28543D; }
    .box-graphics .red-color {
      background: #FE7C66; }

@media (max-width: 1400px) {
  html {
    font-size: 13px; } }

@media (max-width: 1100px) {
  html {
    font-size: 11px; } }

@media (max-width: 640px) {
  html {
    font-size: 6px; } }

